@charset "UTF-8";
* {
  box-sizing: border-box; }

ul, ol, dl {
  margin-top: 0;
  margin-bottom: 0;
  /* clear bootstrap  */ }

ul, ol {
  list-style-type: none; }

ul, ol, dd {
  padding-left: 0; }

p {
  margin: 0; }

i, cite, em, var, address, dfn {
  font-style: normal; }

a {
  cursor: pointer;
  color: inherit; }

a, a:hover, a:focus {
  text-decoration: none; }

img {
  max-width: 100%;
  max-height: 100%; }

/* clear bootstrap */
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
  line-height: inherit; }

table {
  width: 100%; }

label {
  font-weight: normal; }

/* clear input type="number" arrow */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none !important;
  -moz-appearance: textfield;
  margin: 0; }

/* repair bootstrap4 border-width */
.form-control {
  border-width: 1px; }

