@import "_block-comm";

// =========================
// begin  


// global img
.thumbnail, .img {
  display: block;
  text-align: center;
  overflow: hidden;
  img {
    display: inline-block;
    vertical-align: middle;
  }
}
// global 
.ad-box img {
  width: 100%;
}

// global display: table-cell 
.layout-left, .layout-body, .layout-right {
  display: table-cell;
  vertical-align: top;
}
/*  */
.layout-body { width: 10000px; }

// end
